@import url(https://fonts.googleapis.com/css2?family=Rochester);
@import url(https://fonts.googleapis.com/css2?family=Rokkitt);

#root {
  width: 100%;
} 
header {
  width: 100%;
  height: 6em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  align-items: center;
  position: relative;
  top: 1em;
  left: 1em;
  height: 6em;
}

.header-logo-text {
  font-size: 48px;
  color: white;
  font-family: Rochester, cursive;
}

.logo-R {
  position: relative;
  bottom: 0.8em;
  left: 0.3em;
}

.DR-logo {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 6em;
  position: relative;
  bottom: 1em;
}

.header-name-title-container {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  bottom: 1em;
  left: 1em;
}

.header-name-title {
  color: white;
  font-family: Rokkitt;
  font-size: 16px;
}

.header-link {
  height: 6em;
}
footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.white-line {
  width: 85%;
}

.contact-container {
  width: 30%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: .4em;
  margin-left: 5em;
}

.nav-bar-area {
  width: 70%;
  display: flex;
}

nav {
  width: 80%;
  display: flex;
  justify-content: space-around;
}

.nav-bar-item {
  color: white;
  text-decoration: none;
  font-family: Rokkitt;
  margin-top: 1em;
}

.nav-bar-item:hover {
  color: greenyellow;
  transform: scale(1.2)
}

.contact-text {
  color: white;
  font-family: Rokkitt;
}
.home-content-container {
  width: 80%;
  min-height: 70vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}

.summary {
  text-align: center;
  font-size: 24px
}

.headshot {
  width: 20%;
  height: 20em;
}
* {
  margin: 0;
  padding: 0;
}

body {
  background-image: url(/Portfolio/static/media/Background-Portfolio-1.b17a9eef.jpg);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 95vh;
}

nav {
  width: 100%;
}

.white-text {
  color: white;
  font-family: Rokkitt;
}

.text-decoration-none{
  text-decoration: none;
}
.resume-img {
  width: 50%;
  height: 35em;
  position: relative;
  bottom: 2em;
}
.projects-container {
  width: 90%;
  height: 35em;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  overflow-y: auto;
}

.project-card {
  width: 40%;
  height: 28em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2em;
}

.project-card-image {
  width: 120%;
  height: 20em;
}

.project-link {
  width: 50%;
  height: 3em;
  border-radius: 0.5em;
}

.project-card-button {
  width: 100%;
  height: 3em;
  font-family: Rokkitt;
  font-size: 1em;
  border-radius: 0.5em;
}

.project-card-button:hover {
  background-color: greenyellow;
  color: black;
  cursor: pointer;
}
